.textInput {
  display: flex;
  padding: 5px 0;
}

.textInput label {
  width: 50px;
}

.textInput input {
  width: 180px;
  padding: 5px;
  border: #000000 1px solid;
}

.textInput input:hover {
  background-color: #bbbbbb;
}

.textInput input:focus {
  background-color: #fffc20;
}
.appStat {
  font-family: Arial, Helvetica, sans-serif;
}

.appStat .submit {
  width: 242px;
  border: none;
  background-color: #4d88ff;
  padding: 8px 20px;
  margin: 10px 0;
  cursor: pointer;
  color: #ffffff;
}

.appStat .result {
  margin: 20px 0;
}


.appPing {
  font-family: Arial, Helvetica, sans-serif;
}

.appPing .form {
  display: flex;
}

.appPing .textInput label {
  display: none;
}

.appPing .submit {
  width: 60px;
  height: 27px;
  border: none;
  background-color: #4d88ff;
  color: #ffffff;
  cursor: pointer;
  margin: 5px 0 0 5px;
}

